@keyframes shake {
    0% {
        transform: rotate(0deg)
    }
    25% {
        transform: rotate(-8deg);
    }
    50% {
        transform: rotate(8deg);
    }
    75% {
        transform: rotate(-8deg);
    }
    100% {
        transform: rotate(0deg);
    }
}

.cover{
	height: 100vh;
	width: 100vw;
	position: fixed;
	left: 0;top: 0;
	color: white;
    background-color: #ababab;
	.mouse, .mouse-guide, .mouse-mid {
        position: absolute;
        border-radius: 50%;
        pointer-events: none;
        transform: translate(-50%, -50%);
        transition: width 0.2s, height 0.2s, border-width 0.1s, opacity 0.1s;
        transition-delay: 0.05s;
        z-index: 2;
    }

	.mouse {
		opacity: 0.4;
		width: 40px;
		height: 40px;
		border: 20px solid #444758;
	}

	.mouse-mid {
		display: none;
		width: 10px;
		height: 10px;
		background-color: white;
	}

	.mouse-guide {
		width: 5px;
		height: 5px;
		background-color: white;
	}
	&_center {
        left: 50%;
        top: 50%;
        position: absolute;
        transform: translate(-50%, -50%);
    }

	&_btn {
        border-radius: 5rem;
    }
	&_btn img:hover {
		animation-name: shake;
		animation-duration: 1s;
		animation-iteration-count: infinite;
	}

}

