.guide_top{
	transition: all 0.25s;
	position: fixed;left: 260px;right: 0;
	top: 0;height: 65px;border-bottom: 1px solid #eeeeee;
	font-family: "Montserrat";
	display: flex;
	align-items: center;
	padding-left: 35px;font-size: 18px;
	background-color: #fff;
	z-index: 2;
	strong,
	span{
		display: inline-block;
	}
	strong{text-decoration: underline;color: #666;font-weight: 400;position: relative;
		padding-right: 30px;
		margin-right: 25px;
		&:after{
			content:"/";
			position: absolute;right: 0;top: 0;
		}
	}
	.version{
		position: absolute;right: 35px;top: 50%;transform: translateY(-50%);
		border: 1px solid #ddd;
		border-radius: 15px;
		padding: 7px 18px;
		font-weight: 700;
		font-family: 'Montserrat';
		font-size: 12px;
	}
}

.guide{
	&__footer{
		text-align: center;
		font-size: 13px;
		padding: 45px 0;
		border-top: 1px solid #eeeeee;
		position: absolute;bottom: 0;left: 0;right: 0;
	}
	&__title{
		font-weight: 700;
		font-size: 45px;
	}
	&__title2{
		font-weight: 700;
		font-size: 26px;
		margin-bottom: 24px;
		color: #000;
	}
	&__desc{
		color: #666666;
		font-size: 16px;
		padding: 20px 0 50px;
		margin-bottom: 50px;
		line-height: 1.34;
		border-bottom: 1px solid #eeeeee;
	}

	&__font{
		background-color: #f6f8f9;
		border: 1px solid #ddd;
		height: 245px;
		text-align: center;
		border-radius:4px;
		div:nth-child(1){color: #666666;font-size: 14px;padding: 50px 0;}
		div:nth-child(2){font-size: 45px;}
	}

	&__color{
		.color{
			height: 200px;
			position: relative;
			color: #fff;
			border-radius: 4px 4px 0 0;
			span{
				position: absolute;
				left: 25px;
				top: 35px;
			}
			&.v2{
				height: 120px;
			}
		}
		.shame{
			border: 1px solid #e0e0e0;
			border-top: 0;
			padding: 25px;
			border-radius:0 0 4px 4px;
			dl{
				display: flex;
				font-size: 14px;
				dt{
					color: #949494;
					width: 50px;
				}
				dd{
					color: #000;
				}
				+dl{
					margin-top: 15px;
				}
			}
		}
	}

	&__sample{
		border: 1px solid #e0e0e0;
		background-color: #ffffff;
		border-radius: 5px;
		padding: 45px 100px;
		.types{
			font-weight: 700;
			font-size: 14px;
			color: #666666;
			margin-bottom: $gutter-xs;
		}
		.wrapper{
			.btn{
				font-size: 16px;
				padding: 13px 0;
			}
		}
	}

	&__modal{
		background-color: rgba(0,0,0,0.7);
		padding: 50px 160px;	
		border-radius:4px;
	}
}

